import React, { Component } from 'react'
import App from './App'

export default class CPU extends Component {
    state = { 
        messages: "none",
        theme: {
            header: "black",
            footer: "rgba(0, 0, 0, 0.9)",
            message: "rgba(0,133,244,0.9)",
            chatBox: "rgba(0, 133, 244, 0.9)",
            color: "white"
        }
    }
    
    render() {
        return (
            <App message={this.state.messages} theme={this.state.theme}/>
        )
    }
}
